<template>
  <section>
    <span v-for="effectiveTag in meta.effectiveTag" :key="effectiveTag.itemTag"
      >{{ effectiveTag.itemTag
      }}={{ effectiveTag.percent }}=<span class="count" v-if="effectiveTag.percent < 100"
        >({{ effectiveTag.percent }}%)</span
      ></span
    >
  </section>
</template>

<style scoped>
span {
  padding-right: 5px;
}

.count {
  color: #646695;
}
</style>

<script>
export default {
  props: {
    meta: {
      effectiveTag: [
        {
          itemTag: String,
          percent: Number
        },
      ],
      totalCount: Number,
    },
  },
  methods: {
    calculateTagPercent(tagCount) {
      return (tagCount / this.meta.totalCount) * 100.0;
    },
  },
};
</script>